@font-face {
  font-family: "Open Sans";
  font-weight: 300;
  font-style: normal;
  font-display: swap;
  src: local("Open Sans"), url("../fonts/open-sans-v18-latin_cyrillic-300.woff2") format("woff2"), url("../fonts/open-sans-v18-latin_cyrillic-300.woff") format("woff"); }

@font-face {
  font-family: "Open Sans";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: local("Open Sans"), url("../fonts/open-sans-v18-latin_cyrillic-regular.woff2") format("woff2"), url("../fonts/open-sans-v18-latin_cyrillic-regular.woff") format("woff"); }

@font-face {
  font-family: "Open Sans";
  font-weight: 600;
  font-style: normal;
  font-display: swap;
  src: local("Open Sans"), url("../fonts/open-sans-v18-latin_cyrillic-600.woff2") format("woff2"), url("../fonts/open-sans-v18-latin_cyrillic-600.woff") format("woff"); }

@font-face {
  font-family: "Open Sans";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: local("Open Sans"), url("../fonts/open-sans-v18-latin_cyrillic-700.woff2") format("woff2"), url("../fonts/open-sans-v18-latin_cyrillic-700.woff") format("woff"); }

.list-reset {
  padding: 0;
  margin: 0;
  list-style: none; }

.wrapper {
  max-width: 1126px;
  padding: 0 40px;
  margin: 0 auto; }
  @media (max-width: 1023px) {
    .wrapper {
      padding: 0 20px; } }
  @media (max-width: 767px) {
    .wrapper {
      padding: 0 16px; } }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

img {
  max-width: 100%;
  height: auto; }

a {
  text-decoration: none; }

.page {
  height: 100%; }

.page__body {
  display: grid;
  grid-template-rows: -webkit-min-content 1fr -webkit-min-content;
  grid-template-rows: min-content 1fr min-content;
  margin: 0;
  padding: 0;
  min-width: 320px;
  min-height: 100%;
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  line-height: 32px; }

.header {
  background-color: #131314;
  background-image: url("../img/main-background-desktop@1x.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  letter-spacing: -0.005em;
  color: #ffffff; }
  @media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 2dppx) {
    .header {
      background-image: url("../img/main-background-desktop@2x.jpg"); } }
  @media (max-width: 1023px) {
    .header {
      background-image: url("../img/main-background-tablet@1x.jpg"); } }
  @media (max-width: 1023px) and (-webkit-min-device-pixel-ratio: 2), (max-width: 1023px) and (min-resolution: 2dppx) {
    .header {
      background-image: url("../img/main-background-tablet@2x.jpg"); } }
  @media (max-width: 767px) {
    .header {
      background-image: url("../img/main-background-mobile@1x.jpg"); } }
  @media (max-width: 767px) and (-webkit-min-device-pixel-ratio: 2), (max-width: 767px) and (min-resolution: 2dppx) {
    .header {
      background-image: url("../img/main-background-mobile@2x.jpg"); } }

.main-nav__link {
  font-weight: 300;
  color: rgba(255, 255, 255, 0.7); }

.promo__text h2 {
  font-size: 46px;
  line-height: 64px;
  font-weight: 700; }

.promo__text p {
  font-size: 20px;
  font-weight: 300;
  opacity: 0.8; }

.promo__callback p {
  font-size: 17px;
  line-height: 28px;
  font-weight: 600; }

.promo__callback-field input {
  font-size: 15px;
  line-height: 24px;
  color: rgba(28, 28, 28, 0.5); }

.promo__callback-submit {
  font-size: 15px;
  line-height: 21px;
  font-weight: 600;
  background-color: #0ad9c6;
  border: none; }
